#root {
    font-family: 'Arial';
    font-size: 16px;
    background-color: #f6da0a;
    margin: 0px;
}

.container {
    margin: 30px 45px;
    padding: 20px 20px 5px 20px;
    border-radius: 4px;
    background-color: white;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);
}

.container h2 {
    margin-bottom: 10px;
    font-weight: bolder;
    font-size: 20px;
}

nav {
    padding: 15px;
    background: #272727;
    display: flex;
    justify-content: center;
}

nav li {
    align-items: center;
    display: inline;
    color: #FFFFFF;
}

nav a {
    font-size: 20px;
    font-weight: 500;
    padding-right: 20px;
    transition: all 0.5s ease-in-out;
}

nav a:hover {
    color: #fddb3a;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 30px;
    border-bottom: 2px solid #d1d1d1;
}

form Input {
    margin-bottom: 10px;
    box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
}

form label {
    margin-bottom: 10px;
}

form select {
    border: 1px solid #d1d1d1;
    border-radius: 5px;
}

label {
    font-weight: 600;
}

table {
    margin-top: 15px;
    margin-bottom: 15px;
}

img {
    display: block;
    margin-right: auto;
    margin-left: auto;
    width: 20%;
    margin-bottom: 35px;
    border-radius: 50%;
}

#table-student a {
    color: black;
    text-decoration: none;
}

#table-student a:hover {
    color: #cdb22a;
}

#add-btn,
#edit-btn {
    margin-top: 10px;
    margin-bottom: 20px;
    max-width: 150px;
    font-size: 15px;
    background-color: #272727;
    color: #f6da0a;
    border: 1px solid #272727;
    border-radius: 5px;
    transition-duration: 0.3s;
}

#add-btn:hover,
#edit-btn:hover {
    background-color: #f6da0a;
    color: #272727;
    border: 1px solid #272727;
}

#select-filter {
    margin-bottom: 5px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
}

.home-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f6da0a;
}

.home-container h2 {
    font-size: 20px;
    font-weight: bolder;
}

#btn {
    padding: 6px 14px;
    background-color: #272727;
    color: #f6da0a;
    border: 1px solid #f6da0a;
    border-radius: 5px;
    transition-duration: 0.3s;
}

#btn:hover {
    background-color: #f6da0a;
    color: #272727;
    border: 1px solid #272727;
}

.delete-btn {
    font-size: 15px;
}

.card-notfound {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.card-notfound h2 {
    font-weight: bolder;
    font-size: 20px;
}

.footer {
    text-align: center;
    padding: 20px 0px;
    background: #272727;
    color: #FFFFFF;
}